import React,{  PureComponent } from 'react';
import { Row, Card, Form, Input, Button, message, Icon, Checkbox } from 'antd';

import { Link } from 'react-router-dom';


import { connect } from 'react-redux';
import { actionCreators } from './store/index.js';

import './signpage.less';


const FormItem = Form.Item;

class Login extends PureComponent {

    render(){

        const { getFieldDecorator } = this.props.form;
        
        return (
            <div className='SignFlowHomepage'>
                <Row type="flex" justify="center" align="middle">
                    <Card title="ofo" style={{width: 400, marginTop: 90}} headStyle={{textAlign: 'center', fontSize: 20, color: '#0084FF'}}>
                        <Form>

                            <FormItem>
                                {
                                    getFieldDecorator('sys_user_name',{
                                        initialValue:'',
                                        rules:[
                                            {
                                                required:true,
                                                message:''
                                            },
                                            {
                                                min:5,
                                                max:10,
                                                message:', 510'
                                            },
                                            {
                                                pattern:new RegExp('^\\w+$','g'),
                                                message:''
                                            }
                                        ]
                                    })(
                                        <Input prefix={<Icon type="user"/>} placeholder="" />
                                    )
                                }
                            </FormItem>
                            <FormItem>
                                {
                                    getFieldDecorator('password', {
                                        initialValue: '',
                                        rules: []
                                    })(
                                        <Input prefix={<Icon type="lock" />} type="password" placeholder="" />
                                    )
                                }
                            </FormItem>
                            <FormItem>
                                {
                                    getFieldDecorator('remember', {
                                        valuePropName:'checked',
                                        initialValue: true
                                    })(
                                        <Checkbox></Checkbox>
                                    )
                                }
                                <a href="#/" style={{float:'right'}}></a>
                            </FormItem>
                            <FormItem>
                                <Button type="primary" block onClick={this.handleSubmit.bind(this)}></Button>
                                <div><Link to="/register"></Link></div>
                            </FormItem>
                        </Form>
                    </Card>
                </Row>
            </div>
        );
    };
    
    componentDidUpdate(){
        if (this.props.validated === 1){
            this.props.history.push('/admin/home')
        }
    }

    //  
    handleSubmit(){
        let userInfo = this.props.form.getFieldsValue();
        this.props.form.validateFields((err,values)=>{
            if (!err){
                message.success(`Hello!, ${userInfo.sys_user_name}`)
                this.props.loginSubmit(userInfo)
            }
        })
    };

    


};
    
    

// map to props
const mapStateToProps = (state) => ({
    validated: state.getIn(['login', 'validated'])
})

const mapDispatchToProps = (dispatch) => ({
    //
    loginSubmit(userInfo){
        dispatch(actionCreators.loginSubmitAction(userInfo))
    }
});




Login = Form.create()(Login);

export default connect(mapStateToProps, mapDispatchToProps)(Login);