import React, { PureComponent, Fragment } from 'react';
import { Card, Button, Modal, Table} from 'antd';
import Axios from './../../axios';
// import Utils from '/../../utils/utils';

import { connect } from 'react-redux';
import { actionCreators } from './store';

import CreateEmpForm from './createempform.js';
import FilterForm from './filterform.js';


class User extends PureComponent{
    
    state = {
        type: '',
        isVisible:false,
        title: '',
        userInfo: {},
        selectedkeys: [],
        selectedRows: []
    }


    render(){

        const columns = [
            {
                title:'id',
                dataIndex:'id'
            },{
                title: '',
                dataIndex: 'user_name'
            }, {
                title: '',
                dataIndex: 'sex',
                render(sex){
                    return sex === 1?'':''
                }
            }, {
                title: '',
                dataIndex: 'state',
                render(state){
                    return {
                        '1':'',
                        '2':''
                        
                    }[state]
                }
            }, {
                title: '',
                dataIndex: 'birthday'
            }, {
                title: '',
                dataIndex: 'address'
            }
        ];

        const rowSelection = {
            type: 'checkbox',
            selectedRowKeys: this.state.selectedkeys,
            onChange: (selectedRowKeys, selectedRows)=>{
                console.log(selectedRowKeys, selectedRows)
                this.setState({
                    selectedkeys: selectedRowKeys,
                    selectedRows: selectedRows
                })
            }
        };

        const paginationSet = {
            pageSize: 15,
        };

        let footer = {};

        if(this.state.type === 'detail'){
            footer = {
                footer: null
            }
        };

        return (
            <Fragment>

                {/*button*/}
                <Card style={{ marginTop: 10 }} className="operate-wrap">
                    <Button type="primary" icon="plus" onClick={()=>this.hanleOperate('create')}></Button>
                    <Button type="primary" icon="delete" onClick={() => this.hanleOperate('delete')}></Button>
                    <Button type="primary" icon="edit" style={{float:'right'}} onClick={() => this.hanleOperate('edit')}></Button>
                    <Button type="primary" icon="team" style={{float:'right'}} onClick={() => this.hanleOperate('detail')}></Button>
                </Card>

                {/*UI*/}
                <Card>
                    <FilterForm fiterSubmit={this.filterSubmit.bind(this)}/>
                </Card>
                
                {/**/}
                <div className="table-content-wrap">
                    <Table
                        columns={columns}
                        rowSelection={rowSelection}
                        dataSource={this.props.list}
                        pagination={paginationSet}
                    />
                </div>
                
                {/**/}
                <Modal
                    title={this.state.title}
                    width={600}
                    visible={this.state.isVisible}
                    onOk={this.handleSubmit}
                    onCancel={()=>{
                        this.createEmpForm.props.form.resetFields();
                        this.setState({
                            isVisible:false
                        })
                    }}
                    
                    { ...footer }
                >
                    <CreateEmpForm type={this.state.type} userInfo={this.state.userInfo} wrappedComponentRef={(inst)=>{this.createEmpForm = inst;}}/>
                </Modal>
            </Fragment>
        );
    }



    componentDidMount(){
        this.props.requestUserList();
    }

    //
    filterSubmit = (submitInfo)=>{
        this.props.requestUserSingle();
    }


    // Button
    hanleOperate = (type)=>{
        let item =  this.state.selectedRows[0];
        if(type === 'create'){
            this.setState({
                type,
                isVisible:true,
                title:''
            })
        }else if(type === 'edit'){
            if (!item){
                Modal.info({
                    title: "",
                    content: ''
                })
                return;
            }
            this.setState({
                type,
                isVisible: true,
                title: '',
                userInfo:item
            })
        }else if(type === 'detail'){
            if (!item){
                Modal.info({
                    title: "",
                    content: ''
                })
                return;
            }
            this.setState({
                type,
                isVisible: true,
                title: '',
                userInfo: item
            })
        }else{
            if (!item) {
                Modal.info({
                    title: "",
                    content: ''
                })
                return;
            }
            let _this = this;
            Modal.confirm({
                title:'',
                content:'',
                onOk(){
                    Axios.ajax({
                        url:'/emp/delete',
                        params:{
                            id:item.id
                        }
                    }).then((res)=>{
                        if(res.code === 0){
                            _this.setState({
                                isVisible:false
                            })
                            // _this.requestList();
                        }
                    })
                }
            })
        }
    }

    // 
    handleSubmit = ()=>{
        let type = this.state.type;
        let data = this.createEmpForm.props.form.getFieldsValue();
        Axios.ajax({
            url:type ==='create'?'/user/add':'/user/edit',
            params: data
        }).then((res)=>{
            if(res.code === 0){
                this.createEmpForm.props.form.resetFields();
                this.setState({
                    isVisible:false
                })
                // this.requestList();
            }
        })
    };


};


const mapStateToProps = (state)=>({
    list: state.getIn(['user', 'list']).toJS(),
});

const mapDispatchToProps = (dispatch)=>({
    //
    requestUserList(){
        dispatch(actionCreators.requestUserListAction())
    },
    //
    requestEmpSingle(){
        dispatch(actionCreators.requestUserSingleAction())
    }

})


export default connect(mapStateToProps, mapDispatchToProps)(User);