import React, { PureComponent } from 'react';
import { Button, Form, Input, Cascader } from 'antd';

const FormItem = Form.Item;

class FilterForm extends PureComponent {

	render(){
		
		const { getFieldDecorator } = this.props.form;

		const residences = [
				{
				  value: 'jiangsu',
				  label: '',
				  children: [{
				    value: 'nanjing',
				    label: ''
				  }],
				}, {
				  value: 'zhejiang',
				  label: '',
				  children: [{
				    value: 'hangzhou',
				    label: ''
				  }],
				}];

		return (
			<Form layout="inline">

				<FormItem label="">
		          {
		          	getFieldDecorator('city_name')(
		            	<Cascader options={residences} />
		          	)
		          }
		        </FormItem>

				<FormItem>or</FormItem>

				<FormItem label="">
		          {
		          	getFieldDecorator('bike_number')(
		            	<Input />
		          	)
		          }
		        </FormItem>


				<FormItem>
					<Button type="primary" style={{margin:'0 20px'}} onClick={()=>(this.filterSubmitForm())}></Button>
					<Button></Button>
				</FormItem>

			</Form>
		)
	};
	
	filterSubmitForm(){
		let submitInfo = this.props.form.getFieldsValue();
		this.props.filterSubmit(submitInfo);
	}

};

FilterForm=Form.create({})(FilterForm);

export default FilterForm;