import React, { PureComponent, Fragment } from 'react';
import  { Card, Button, Table, Form, Modal, message } from 'antd';
import Axios from '../../axios/index.js';

import { connect } from 'react-redux';
import { actionCreators } from './store';

import FilterForm from './filterform.js';



const FormItem = Form.Item;

class Order extends PureComponent{

	render(){
		const columns = [
			{
				title:'',
				dataIndex:'order_sn'
			},{
				title:'',
				dataIndex:'bike_sn'
			},{
				title:'',
				dataIndex:'user_name'
			},{
				title:'',
				dataIndex:'mobile'
			},{
				title:'',
				dataIndex:'distance'
			},{
				title:'',
				dataIndex:'total_time'
			},{
				title:'',
				dataIndex:'status'
			},{
				title:'',
				dataIndex:'start_time'
			},{
				title:'',
				dataIndex:'end_time'
			},{
				title:'',
				dataIndex:'total_fee'
			},{
				title:'',
				dataIndex:'user_pay'
			},

		];
		
		const formItemLayout = {
			labelCol: {span:5},
			wrapperCol: {span:19}
		}
		
		const rowSelection = {
			type: 'radio',
			selectedRowKeys: this.state.selectedRowKeys,
			onChange: (selectedRowKeys, selectedRows)=>{
				this.setState({
					selectedRowKeys: selectedRowKeys,
					selectedRows: selectedRows
				})
			}
		};

		const paginationSet = {
            pageSize: 15,
        };

		return (
			<Fragment>
				<Card>
					<Button type="primary" onClick={this.openOrderDetail}></Button>
					<Button onClick={this.handleConfirm} style={{margin:'0 20px'}}></Button>
				</Card>

				<Card>
					<FilterForm />
				</Card>

				<div className="table-content-wrap">
					<Table
						columns={columns}
						rowSelection={rowSelection}
						dataSource={this.props.list}
						pagination={paginationSet}

					/>
				</div>

				<Modal
					title=""
					width={600}
					visible={this.state.orderConfirmVisible}
					onCancel={()=>{
						this.setState({
							orderConfirmVisible: false
						})
					}}
					onOk={this.handleFinishOrder}
				>
					<Form layout="horizontal">
						<FormItem label="" {...formItemLayout}>{this.state.orderInfo.bike_sn}</FormItem>
						<FormItem label="" {...formItemLayout}>{this.state.orderInfo.start_time}</FormItem>
						<FormItem label="" {...formItemLayout}>{this.state.orderInfo.location}</FormItem>
					</Form>
				</Modal>
			</Fragment>
		);

	};


	state = {
		orderConfirmVisible: false,
		orderInfo: {},
		selectedRowKeys: [],
		selectedRows: []
	};

	//
	componentDidMount(){
		this.props.requestOrderList()
	};

	// 
	openOrderDetail = ()=>{
		let item = this.state.selectedRowKeys;
		if(!item){
			Modal.info({
				title:'',
				content:''
			})
			return
		}

		window.open(`/#/common/order/detail/${item}`, '_blank')
	}	
	
	// modal 
	handleConfirm = ()=>{
        let item = this.state.selectedItem;
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            })
            return;
        }
        Axios.ajax({
            url:'/order/ebike_info',
            data:{
                params:{
                    orderId: item.id
                }
            }
        }).then((res)=>{
            if(res.code === 0 ){
                this.setState({
                    orderInfo:res.result,
                    orderConfirmVisble: true
                })
            }
        })
    };
	
	//
	handleFinishOrder = ()=>{
		let item = this.state.selectedItem;
        Axios.ajax({
            url: '/order/finish_order',
            data: {
                params: {
                    orderId: item.id
                }
            }
        }).then((res) => {
            if (res.code === 0) {
                message.success('')
                this.setState({
                    orderConfirmVisble: false
                })
                this.requestList();
            }
        })
	};

};

const mapStateToProps = (state)=>({
	list: state.getIn(['order', 'list']).toJS()
});

const mapDispatchToProps = (dispatch)=>({
	//
	requestOrderList(){
		dispatch(actionCreators.requestOrderListAction())
	}
})


export default connect(mapStateToProps, mapDispatchToProps)(Order);