import React, { PureComponent } from 'react';
import { Button, Form, Select, Cascader } from 'antd';

const FormItem = Form.Item;
const Option = Select.Option;

class FilterForm extends PureComponent{

	render(){
		
		const { getFieldDecorator } = this.props.form;

		const residences = [
				{
				  value: '',
				  label: '',
				  children: [{
				    value: '',
				    label: ''
				  }],
				}, {
				  value: '',
				  label: '',
				  children: [{
				    value: '',
				    label: ''
				  }],
				}];

		return (
			<Form layout="inline">

				<Form.Item label="">
		          {
		          	getFieldDecorator('city_name')(
		            	<Cascader options={residences} />
		          	)
		          }
		        </Form.Item>

				<FormItem label="">
					{
						getFieldDecorator('mode')(
							<Select palceholder="" style={{width:130}}>
								<Option value=""></Option>
								<Option value="1"></Option>
								<Option value="2"></Option>
							</Select>
						)
					}
				</FormItem>

				<FormItem label="">
					{
						getFieldDecorator('op_mode')(
							<Select palceholder="" style={{width:80}}>
								<Option value=""></Option>
								<Option value="1"></Option>
								<Option value="2"></Option>
							</Select>
						)
					}
				</FormItem>

				<FormItem label="">
					{
						getFieldDecorator('auth_status')(
							<Select palceholder="" style={{width:90}}>
								<Option value=""></Option>
								<Option value="1"></Option>
								<Option value="2"></Option>
							</Select>
						)
					}
				</FormItem>

				<FormItem>
					<Button type="primary" style={{margin:'0 20px'}} onClick={()=>(this.filterSubmitForm())}></Button>
					<Button></Button>
				</FormItem>

			</Form>
		)
	};
	
	filterSubmitForm(){
		let submitInfo = this.props.form.getFieldsValue();
		this.props.filterSubmit(submitInfo);
	}

};

FilterForm=Form.create({})(FilterForm);

export default FilterForm;