import React, { PureComponent } from 'react';
import { Form, Input, Select, Cascader } from 'antd';

const FormItem = Form.Item;
const Option = Select.Option;

class OpenCityForm extends PureComponent{
	
	render(){

		const { getFieldDecorator } = this.props.form;
		
		const modalFormItemLayout = {
			labelCol:{
				span:5
			},
			wrapperCol:{
				span:19
			}
		};
		
		const residences = [
				{
				  value: '',
				  label: '',
				  children: [{
				    value: '',
				    label: ''
				  }],
				},{
				  value: '',
				  label: '',
				  children: [{
				    value: '',
				    label: ''
				  },{
				    value: '',
				    label: ''
				  }],
				},{
				  value: '',
				  label: '',
				  children: [{
				    value: '',
				    label: ''
				  }],
				}
			];

		return (
			<Form layout="horizontal">
				<FormItem label="" {...modalFormItemLayout}>
					{
			          	getFieldDecorator('city_name')(
			            	<Cascader options={residences} style={{ width: 200 }}/>
			          	)
		          	}
				</FormItem>

				<FormItem label="" {...modalFormItemLayout}>
					{
						getFieldDecorator('mode',{
							initialValue:'1'
						})(
							<Select style={{ width: 150 }}>
								<Option value="0"></Option>
								<Option value="1"></Option>
								<Option value="2"></Option>
							</Select>
						)
					}
				</FormItem>

				<FormItem label="" {...modalFormItemLayout}>
					{
						getFieldDecorator('op_mode',{
							initialValue:'1'
						})(
							<Select style={{ width: 100 }}>
								<Option value="1"></Option>
								<Option value="2"></Option>
							</Select>	
						)
					}
				</FormItem>

				<FormItem label="" {...modalFormItemLayout}>
					{
						getFieldDecorator('city_admin',{
							initialValue:''
						})(
							<Input placeholder="" style={{ width: 200 }}/>
						)
					}
				</FormItem>
				
				<FormItem label="ID" {...modalFormItemLayout}>
					{
						getFieldDecorator('city_admin_id',{
							initialValue:''
						})(
							<Input placeholder="id" style={{ width: 200 }}/>
						)
					}
				</FormItem>

			</Form>
		)
	}
};

OpenCityForm = Form.create({})(OpenCityForm);

export default OpenCityForm;