import React, { PureComponent } from 'react';
import  { Button, Form, Select, DatePicker } from 'antd';

//  
const FormItem = Form.Item;
const Option = Select.Option;

class FilterForm extends PureComponent{
	
	// 
	handleFilterSubmit = ()=>{
		// console.log(this);
        let fieldsValue = this.props.form.getFieldsValue(['city_id']);
        console.log(fieldsValue)
    };

	render(){
		
		const { getFieldDecorator } = this.props.form;
		return (
			<Form layout="inline">

				<FormItem label="">
					{
						getFieldDecorator('city_id')(
							<Select palceholder="" style={{width:90}}>
								<Option value="1"></Option>
								<Option value="2"></Option>
								<Option value="3"></Option>
								<Option value="4"></Option>
							</Select>
						)
					}
				</FormItem>

				<FormItem label="">
					{
						getFieldDecorator('start_time')(
							<DatePicker 
								showTime
								format="YYYY-MM-DD HH:mm"
      							placeholder="From"
							    // onChange={}
							    // onOk={}
							/>
						)
					}
				</FormItem>

				<FormItem>
					{
						getFieldDecorator('end_time')(
							<DatePicker 
								showTime
								format="YYYY-MM-DD HH:mm"
      							placeholder="To"
							    // onChange={}
							    // onOk={}
							/>
						)
					}
				</FormItem>

				<FormItem label="">
					{
						getFieldDecorator('status')(
							<Select palceholder="" style={{width:80}}>
								<Option value=""></Option>
								<Option value="1"></Option>
								<Option value="2"></Option>
							</Select>
						)
					}
				</FormItem>

				<FormItem>
					<Button type="primary" style={{margin:'0 20px'}} onClick={this.handleFilterSubmit}></Button>
					<Button></Button>
				</FormItem>

			</Form>
		)
	};
};

FilterForm=Form.create({})(FilterForm);

export default FilterForm;