import React, { PureComponent } from 'react';

import {Card, Form, Row, Button, Input, Radio, Select, DatePicker, message } from 'antd';
import './signpage.less';

import moment from 'moment';
import Axios from '../../axios/index.js';


const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const Option = Select.Option;
const TextArea = Input.TextArea;


class Register extends PureComponent {

    render(){

        const { getFieldDecorator } = this.props.form;
        
        //formitem 
        const formItemLayout = {
            labelCol:{
                xs:24,
                sm:6
            },
            wrapperCol:{
                xs:24,
                sm:12
            }
        }

        //formitem 
        const offsetLayout = {
            wrapperCol:{
                xs:24,
                sm:{
                    span:12,
                    offset:6
                }
            }
        }
        
        return (
            <div className='SignFlowHomepage'>
                <Row type="flex" justify="center" align="middle">
                    <Card title="" style={{width: 700, marginTop: 90}}>
                        <Form layout="horizontal">

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('sys_user_name', {
                                        initialValue: '',
                                        rules: [
                                            {
                                                required: true,
                                                message: ''
                                            }
                                        ]
                                    })(
                                        <Input placeholder="" />
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('password', {
                                        initialValue: '',
                                        rules: [
                                            {
                                                required: true,
                                                message: ''
                                            }
                                        ]
                                    })(
                                        <Input type="password" placeholder="" />
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('cadmin_name', {
                                        initialValue: '',
                                        rules: [
                                            {
                                                required: true,
                                                message: ''
                                            }
                                        ]
                                    })(
                                        <Input placeholder="" />
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('phone_number', {
                                        initialValue: '',
                                        rules: [
                                            {
                                                required: true,
                                                message: ''
                                            }
                                        ]
                                    })(
                                        <Input placeholder="" />
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('sex', {
                                        initialValue: '1'
                                    })(
                                        <RadioGroup>
                                            <Radio value="1"></Radio>
                                            <Radio value="2"></Radio>
                                        </RadioGroup>
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('state', {
                                        initialValue: '1'
                                    })(
                                        <Select>
                                            <Option value="1"></Option>
                                            <Option value="2"></Option>
                                        </Select>
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('birthday')(
                                        <DatePicker
                                        />
                                    )
                                }
                            </FormItem>

                            <FormItem label="" {...formItemLayout}>
                                {
                                    getFieldDecorator('address',{
                                        initialValue:''
                                    })(
                                        <TextArea
                                            autosize={{
                                                minRows: 4, maxRows: 6
                                            }}
                                        />
                                    )
                                }
                            </FormItem>

                            <FormItem {...offsetLayout}>
                                <Button type="primary" onClick={this.handleSubmit} style={{float:'right'}}></Button>
                            </FormItem>

                        </Form>
                    </Card>
                </Row>
            </div>
        );
    
    };
    
    //  
    handleSubmit= ()=>{
        let userInfo = this.props.form.getFieldsValue();
        userInfo.birthday = moment(userInfo.birthday).format('YYYY-MM-DD')
        Axios.ajax_server({
          method: 'post',
          url: '/register',
          data: userInfo
        }).then((data)=>{
            if (data.code === 0){
                message.success('!')
                this.props.history.push('/')
            }
        })


    }
};

Register = Form.create()(Register);


export default Register;